import React from 'react'
import { useQuery } from 'react-query'
import { useNavigate } from 'react-router-dom'
import { 
  TrendingUp, 
  Users, 
  FolderOpen, 
  CheckCircle, 
  AlertTriangle,
  Plus,
  Calendar,
  BarChart3,
  ArrowUpRight,
  ArrowDownRight,
  Target,
  Star,
  Activity
} from 'lucide-react'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'

import DashboardLayout from '@/components/DashboardLayout'
import api from '@/services/api'

interface DashboardStats {
  totalProjects: number
  totalTasks: number
  completedTasks: number
  overdueTasks: number
  upcomingTasks: number
  completionRate: number
}

interface RecentTask {
  id: string
  title: string
  status: string
  priority: string
  dueDate: string
  project: {
    name: string
    color: string
  }
}

const DashboardPage: React.FC = () => {
  const navigate = useNavigate()

  // Fetch dashboard stats
  const { data: statsData } = useQuery<{ data: DashboardStats }>(
    'dashboard-stats',
    () => api.get('/api/dashboard/stats').then(res => res.data),
    {
      refetchInterval: 30000,
    }
  )

  // Fetch recent tasks
  const { data: tasksData, isLoading: tasksLoading } = useQuery<{ data: { tasks: RecentTask[] } }>(
    'recent-tasks',
    () => api.get('/api/tasks?limit=5').then(res => res.data),
    {
      refetchInterval: 30000,
    }
  )

  const stats = statsData?.data || {
    totalProjects: 0,
    totalTasks: 0,
    completedTasks: 0,
    overdueTasks: 0,
    upcomingTasks: 0,
    completionRate: 0
  }

  const recentTasks = tasksData?.data.tasks || []

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'COMPLETED':
        return 'bg-emerald-500'
      case 'IN_PROGRESS':
        return 'bg-blue-500'
      case 'TODO':
        return 'bg-slate-400'
      default:
        return 'bg-slate-400'
    }
  }

  const StatCard = ({ 
    title, 
    value, 
    icon, 
    trend, 
    trendValue, 
    subtitle,
    color = 'primary'
  }: {
    title: string
    value: string | number
    icon: React.ReactNode
    trend?: 'up' | 'down'
    trendValue?: string
    subtitle?: string
    color?: string
  }) => (
    <Card className="group hover:shadow-md transition-all duration-200 border-border/50">
      <CardContent className="p-6">
        <div className="flex items-start justify-between">
          <div className="flex items-start space-x-4">
            <div className={`p-3 rounded-lg bg-${color}/10 group-hover:scale-105 transition-transform duration-200 flex-shrink-0`}>
              {icon}
            </div>
            <div className="min-w-0 flex-1">
              <p className="text-sm font-medium text-muted-foreground mb-1">{title}</p>
              <p className="text-2xl font-bold tracking-tight leading-none">{value}</p>
              {subtitle && (
                <p className="text-xs text-muted-foreground mt-2">{subtitle}</p>
              )}
            </div>
          </div>
          {trend && trendValue && (
            <div className={`flex items-center space-x-1 text-sm font-medium flex-shrink-0 ${
              trend === 'up' ? 'text-emerald-600' : 'text-red-600'
            }`}>
              {trend === 'up' ? (
                <ArrowUpRight className="h-4 w-4" />
              ) : (
                <ArrowDownRight className="h-4 w-4" />
              )}
              <span>{trendValue}</span>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  )

  const TaskCard = ({ task }: { task: RecentTask }) => (
    <div className="flex items-center space-x-4 p-4 rounded-lg border border-border/50 bg-card hover:bg-accent/30 transition-colors duration-200">
      <div className={`w-2 h-2 rounded-full flex-shrink-0 ${getStatusColor(task.status)}`} />
      <div className="flex-1 min-w-0">
        <p className="text-sm font-medium leading-none truncate mb-1">{task.title}</p>
        <div className="flex items-center space-x-2 flex-wrap gap-y-1">
          <Badge variant="outline" className="text-xs border-border/50">
            {task.project.name}
          </Badge>
          <Badge variant="secondary" className="text-xs">
            {task.priority}
          </Badge>
        </div>
      </div>
      <div className="text-right flex-shrink-0">
        <p className="text-xs text-muted-foreground">
          {new Date(task.dueDate).toLocaleDateString()}
        </p>
      </div>
    </div>
  )

  return (
    <DashboardLayout>
      <div className="space-y-8 p-6 lg:p-8 max-w-7xl mx-auto">
        {/* Header Section */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div className="space-y-1">
            <h1 className="text-2xl sm:text-3xl font-bold tracking-tight">System Overview</h1>
            <p className="text-muted-foreground text-sm sm:text-base">
              Comprehensive view of project management system performance and metrics
            </p>
          </div>
          <div className="flex items-center space-x-3 flex-shrink-0">
            <Button variant="outline" size="sm" className="hidden sm:flex">
              <Calendar className="h-4 w-4 mr-2" />
              Calendar View
            </Button>
            <Button variant="outline" size="sm">
              <BarChart3 className="h-4 w-4 mr-2" />
              Analytics
            </Button>
          </div>
        </div>

        {/* Stats Grid */}
        <div className="grid gap-6 grid-cols-1 sm:grid-cols-2 lg:grid-cols-4">
          <StatCard
            title="Active Projects"
            value={stats.totalProjects}
            icon={<FolderOpen className="h-5 w-5 text-primary" />}
            trend="up"
            trendValue="+12%"
            subtitle="Total active projects"
            color="primary"
          />
          <StatCard
            title="Total Tasks"
            value={stats.totalTasks}
            icon={<Target className="h-5 w-5 text-blue-600" />}
            trend="up"
            trendValue="+8%"
            subtitle="All system tasks"
            color="blue"
          />
          <StatCard
            title="Completed Tasks"
            value={stats.completedTasks}
            icon={<CheckCircle className="h-5 w-5 text-emerald-600" />}
            trend="up"
            trendValue="+15%"
            subtitle="Successfully completed"
            color="emerald"
          />
          <StatCard
            title="Overdue Tasks"
            value={stats.overdueTasks}
            icon={<AlertTriangle className="h-5 w-5 text-red-600" />}
            trend="down"
            trendValue="-5%"
            subtitle="Require attention"
            color="red"
          />
        </div>

        {/* Main Content Grid */}
        <div className="grid gap-8 grid-cols-1 lg:grid-cols-7">
          {/* Progress Section */}
          <Card className="lg:col-span-4 border-border/50">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl font-semibold">System Performance</CardTitle>
              <CardDescription>
                Overall project completion rates and system efficiency metrics
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-3">
                <div className="flex items-center justify-between">
                  <span className="text-sm font-medium">Completion Rate</span>
                  <span className="text-sm font-bold">{stats.completionRate}%</span>
                </div>
                <Progress value={stats.completionRate} className="h-2" />
                <div className="flex items-center justify-between text-xs text-muted-foreground">
                  <span>{stats.completedTasks} completed</span>
                  <span>{stats.totalTasks - stats.completedTasks} remaining</span>
                </div>
              </div>
              
              <Separator className="my-6" />
              
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <div className="flex items-center space-x-2">
                    <div className="w-3 h-3 rounded-full bg-emerald-500 flex-shrink-0" />
                    <span className="text-sm font-medium">On Schedule</span>
                  </div>
                  <p className="text-2xl font-bold">{stats.totalTasks - stats.overdueTasks}</p>
                  <p className="text-xs text-muted-foreground">Tasks on schedule</p>
                </div>
                <div className="space-y-2">
                  <div className="flex items-center space-x-2">
                    <div className="w-3 h-3 rounded-full bg-red-500 flex-shrink-0" />
                    <span className="text-sm font-medium">At Risk</span>
                  </div>
                  <p className="text-2xl font-bold">{stats.overdueTasks}</p>
                  <p className="text-xs text-muted-foreground">Tasks overdue</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <Card className="lg:col-span-3 border-border/50">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl font-semibold">Administrative Actions</CardTitle>
              <CardDescription>
                Quick access to system management functions
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-3">
              <Button className="w-full justify-start h-11" onClick={() => navigate('/projects')}>
                <Plus className="h-4 w-4 mr-3" />
                Create Project
              </Button>
              <Button variant="outline" className="w-full justify-start h-11" onClick={() => navigate('/tasks')}>
                <Target className="h-4 w-4 mr-3" />
                Create Task
              </Button>
              <Button variant="outline" className="w-full justify-start h-11">
                <Users className="h-4 w-4 mr-3" />
                Manage Users
              </Button>
              <Button variant="outline" className="w-full justify-start h-11">
                <Calendar className="h-4 w-4 mr-3" />
                Schedule Meeting
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Recent Activity */}
        <Card className="border-border/50">
          <CardHeader className="pb-4">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
              <div className="space-y-1">
                <CardTitle className="text-xl font-semibold">Recent Task Activity</CardTitle>
                <CardDescription>
                  Latest task updates and system activities
                </CardDescription>
              </div>
              <Button variant="outline" size="sm" onClick={() => navigate('/tasks')} className="flex-shrink-0">
                View All Tasks
              </Button>
            </div>
          </CardHeader>
          <CardContent>
            {tasksLoading ? (
              <div className="flex items-center justify-center py-12">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
              </div>
            ) : recentTasks.length === 0 ? (
              <div className="text-center py-12">
                <Target className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                <h3 className="text-lg font-semibold mb-2">No tasks available</h3>
                <p className="text-muted-foreground mb-6 max-w-md mx-auto">
                  Create the first task to begin project management operations
                </p>
                <Button onClick={() => navigate('/tasks')}>
                  <Plus className="h-4 w-4 mr-2" />
                  Create Task
                </Button>
              </div>
            ) : (
              <div className="space-y-3">
                {recentTasks.map((task) => (
                  <TaskCard key={task.id} task={task} />
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Performance Metrics */}
        <div className="grid gap-6 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3">
          <Card className="border-border/50">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-3">
              <CardTitle className="text-sm font-medium">System Velocity</CardTitle>
              <TrendingUp className="h-4 w-4 text-muted-foreground flex-shrink-0" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold mb-1">24.5</div>
              <p className="text-xs text-muted-foreground">
                +2.1 from previous period
              </p>
            </CardContent>
          </Card>
          
          <Card className="border-border/50">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-3">
              <CardTitle className="text-sm font-medium">Sprint Progress</CardTitle>
              <Activity className="h-4 w-4 text-muted-foreground flex-shrink-0" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold mb-1">78%</div>
              <p className="text-xs text-muted-foreground">
                3 days remaining
              </p>
            </CardContent>
          </Card>
          
          <Card className="border-border/50 sm:col-span-2 lg:col-span-1">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-3">
              <CardTitle className="text-sm font-medium">System Quality</CardTitle>
              <Star className="h-4 w-4 text-muted-foreground flex-shrink-0" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold mb-1">A+</div>
              <p className="text-xs text-muted-foreground">
                98% test coverage
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    </DashboardLayout>
  )
}

export default DashboardPage 